olor(alt),.99);\n}\n\n.UiTheme {\n  & {\n    transition: 0s ease-out;\n  }\n  & { @include UI_THEMES_EXTEND; }\n}","\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n@import '~@/styles/main';\n@import '~@/styles/extend/themes';\n\nimg[src*='preloader'][src*='.gif'] {\n  width: 3rem;\n  height: 3rem;\n  opacity: 0.3;\n}\n\np, li {\n  br {\n    content: \"\";\n    display: block;\n    height: 0;\n    width: 100%;\n    margin: -.5em 0;\n  }\n}\n"],"sourceRoot":""}]);

